html, body {
  margin: 0;
}

/* nunito-regular - latin */
@font-face {
  font-family: 'Nunito';
  font-style: normal;
  font-weight: 400;
  src: url('media/fonts/nunito/nunito-v16-latin-regular.eot'); /* IE9 Compat Modes */
  src: local(''),
       url('media/fonts/nunito/nunito-v16-latin-regular.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
       url('media/fonts/nunito/nunito-v16-latin-regular.woff2') format('woff2'), /* Super Modern Browsers */
       url('media/fonts/nunito/nunito-v16-latin-regular.woff') format('woff'), /* Modern Browsers */
       url('media/fonts/nunito/nunito-v16-latin-regular.ttf') format('truetype'), /* Safari, Android, iOS */
       url('media/fonts/nunito/nunito-v16-latin-regular.svg#Nunito') format('svg'); /* Legacy iOS */
}
